html,
body {
    height: 100vh;
    width: 100vw;
    padding: 0;
    margin: 0;
    overflow: hidden;
    user-select: none !important;
    /* font-family: GothLight; */
}
body {
    background-image: radial-gradient(#0e272c 5%, #000f1e);
}

.App {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.wrapper {
    position: relative;
    width: 500px;
    height: 500px;
}

.wrapped_lottie {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 500px;
    height: 500px;
    transform: scale(0.8);
}

.wrapped_svg_around1 {
    position: absolute;
    top: -10%;
    left: 45%;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.wrapped_svg_around2 {
    position: absolute;
    top: 18%;
    right: -5%;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.wrapped_svg_around3 {
    position: absolute;
    top: 18%;
    left: -5%;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.wrapped_svg_around4 {
    position: absolute;
    top: 75%;
    left: -5%;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.wrapped_svg_around5 {
    position: absolute;
    top: 75%;
    right: -5%;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.wrapped_svg_around6 {
    position: absolute;
    bottom: -10%;
    left: 45%;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.content {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 50%;
    top: 49%;
    transform: translate(-50%, -50%);
    color: white !important;
    font-size: 14px;
    width: 45%;
    height: 55%;
    z-index: 99999999 !important;
}

@media all and (max-width: 699px) and (min-width: 520px) {
    .wrapper {
        width: 400px;
        height: 400px;
    }

    .wrapper_lottie {
        width: 400px;
        height: 400px;
    }
    .wrapped_svg_around1 {
        width: 30px;
        height: 30px;
    }
    .wrapped_svg_around2 {
        width: 30px;
        height: 30px;
    }
    .wrapped_svg_around3 {
        width: 30px;
        height: 30px;
    }
    .wrapped_svg_around4 {
        width: 30px;
        height: 30px;
    }
    .wrapped_svg_around5 {
        width: 30px;
        height: 30px;
    }
    .wrapped_svg_around6 {
        width: 30px;
        height: 30px;
    }
    .content {
        font-size: 12px;
    }
}

@media all and (max-width: 519px) {
    .wrapper {
        width: 250px;
        height: 250px;
    }

    .wrapper_lottie {
        width: 250px;
        height: 250px;
    }
    .wrapped_svg_around1 {
        width: 18px;
        height: 18px;
    }
    .wrapped_svg_around2 {
        width: 18px;
        height: 18px;
    }
    .wrapped_svg_around3 {
        width: 18px;
        height: 18px;
    }
    .wrapped_svg_around4 {
        width: 18px;
        height: 18px;
    }
    .wrapped_svg_around5 {
        width: 18px;
        height: 18px;
    }
    .wrapped_svg_around6 {
        width: 18px;
        height: 18px;
    }
    .content {
        font-size: 8px;
    }
}

.fade-in {
    -webkit-animation: fade-in 1.5s cubic-bezier(0.39, 0.575, 0.565, 1) both;
    animation: fade-in 1.5s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 1;
    }
    35% {
        opacity: 0;
    }
    65% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes fade-in {
    0% {
        opacity: 1;
    }
    35% {
        opacity: 0;
    }
    65% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
